.nav-title {
    font-size: 22px;
    /* font-weight: 700; */
    display: inline-block;
    color: #f2f2f2;
    padding-left: 10px;
    font-family: 'Open Sans'
}

.nav {
  overflow: hidden;
  background-color: #008391;
}

.nav-link {
  font-family: 'Open Sans';
  float: left;
  color: #f2f2f2;
  background-color: #008391;
  text-align: center;
  padding: 24px 10px;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 14px;

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  background-color: #4fb3c1;
  /* color: #4fb3c1; */
}

.nav-right {
  float: right;
}
footer {
    margin-top: 30px;
    padding-bottom: 10px;
    padding-top: 10px;
    text-align: center;
    text-transform: uppercase;
    width: 100%;
    font-size: 12px;
    color: grey;
    font-family: 'Open Sans';
}

body {
    margin: 0px;
}

h4 {
    text-transform: uppercase;
    text-align: center;
    padding-top: 25px;
    padding-bottom: 15px;
    font-size: 32px !important;
}

ul {
    width: 100%;
}

li {
    list-style-type: none;
    text-align: center !important;
}

.alert {
    margin: 50px;
    font-family: 'Open Sans';
}

form {
    /* background-color: #dbd5d5; */
    /* width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px; */
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #008391;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
  font-size: 18px;
  font-family: 'Open Sans';
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #0d3636;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    /* border-bottom:1px solid #444;  */
    cursor: pointer;
}

